'use client';

import { useEffect, useCallback } from 'react';
import { X, ExternalLink, Calendar, Palette, Tag } from 'lucide-react';
import { Artwork } from '../types/artwork';
import { getImageUrl } from '@/lib/utils';

interface LightboxProps {
    artwork: Artwork | null;
    isOpen: boolean;
    onClose: () => void;
}

export default function Lightbox({ artwork, isOpen, onClose }: LightboxProps) {
    // Close on escape key
    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        if (e.key === 'Escape') onClose();
    }, [onClose]);

    useEffect(() => {
        if (isOpen) {
            document.addEventListener('keydown', handleKeyDown);
            document.body.style.overflow = 'hidden';
        }

        return () => {
            document.removeEventListener('keydown', handleKeyDown);
            document.body.style.overflow = 'unset';
        };
    }, [isOpen, handleKeyDown]);

    if (!isOpen || !artwork) return null;

    const imageUrl = getImageUrl(artwork.cdnUrl, artwork.imageUrl, artwork.id);
    
    const formattedDate = artwork.createdAt instanceof Date
        ? artwork.createdAt.toLocaleDateString('en-US', {
            year: 'numeric',
            month: 'short',
            day: 'numeric'
        })
        : '';

    return (
        <div 
            className="fixed inset-0 z-[200] bg-black/95 backdrop-blur-sm"
            onClick={onClose}
        >
            {/* Close Button - Top Right */}
            <button
                onClick={onClose}
                className="fixed top-4 right-4 z-[210] p-3 bg-white/10 hover:bg-white/20 text-white rounded-full transition-all duration-200 group"
                aria-label="Close"
            >
                <X className="w-6 h-6 group-hover:rotate-90 transition-transform duration-200" />
            </button>

            {/* Open Original - Top Left */}
            <button
                onClick={(e) => {
                    e.stopPropagation();
                    window.open(imageUrl, '_blank');
                }}
                className="fixed top-4 left-4 z-[210] px-4 py-2 bg-white/10 hover:bg-white/20 text-white rounded-full transition-all duration-200 flex items-center gap-2 text-sm font-medium"
            >
                <ExternalLink className="w-4 h-4" />
                Open Original
            </button>

            {/* Main Content Container */}
            <div 
                className="w-full h-full flex flex-col"
                onClick={(e) => e.stopPropagation()}
            >
                {/* Image Container - Takes most of the space */}
                <div className="flex-1 flex items-center justify-center p-4 pt-16 pb-4 min-h-0">
                    {/* eslint-disable-next-line @next/next/no-img-element */}
                    <img
                        src={imageUrl}
                        alt={artwork.title}
                        className="max-w-full max-h-full object-contain rounded-lg shadow-2xl"
                        onClick={(e) => e.stopPropagation()}
                    />
                </div>

                {/* Info Bar - Bottom */}
                <div 
                    className="bg-black/80 backdrop-blur-md border-t border-white/10 px-6 py-4"
                    onClick={(e) => e.stopPropagation()}
                >
                    <div className="max-w-6xl mx-auto">
                        {/* Title Row */}
                        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-3 mb-3">
                            <h2 className="text-xl sm:text-2xl font-bold text-white">
                                {artwork.title}
                            </h2>
                            
                            {/* Meta Info */}
                            <div className="flex flex-wrap items-center gap-3 text-sm text-white/70">
                                {formattedDate && (
                                    <span className="flex items-center gap-1.5">
                                        <Calendar className="w-4 h-4" />
                                        {formattedDate}
                                    </span>
                                )}
                                {artwork.medium && (
                                    <span className="px-2 py-0.5 bg-white/10 rounded-full text-white/90">
                                        {artwork.medium}
                                    </span>
                                )}
                                <span className="px-2 py-0.5 bg-[var(--accent-primary)]/30 text-[var(--accent-primary)] rounded-full">
                                    {artwork.category}
                                </span>
                            </div>
                        </div>

                        {/* Description & Tags Row */}
                        <div className="flex flex-col sm:flex-row gap-4 sm:items-start">
                            {artwork.description && (
                                <p className="text-white/60 text-sm flex-1 line-clamp-2">
                                    {artwork.description}
                                </p>
                            )}
                            
                            {artwork.tags && artwork.tags.length > 0 && (
                                <div className="flex flex-wrap gap-1.5 sm:justify-end shrink-0">
                                    {artwork.tags.slice(0, 5).map((tag) => (
                                        <span
                                            key={tag}
                                            className="inline-flex items-center gap-1 px-2 py-0.5 bg-white/5 text-white/50 text-xs rounded-full"
                                        >
                                            <Tag className="w-3 h-3" />
                                            {tag}
                                        </span>
                                    ))}
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
